import React from 'react';
import '../Home/Home.css';

const Home = () => {

    const projects = {
        youtube: {
            image: require("../../images/youtube.png"),
            header: "Youtube Clone",
            description: "Use the Youtube api to load 5 videos to select from to view according to your search criteria.",
        },
        stockImage: {
            image: require("../../images/stock_photos.png"),
            header: "Stock Image Search",
            description: `Allows you to search for free for use stock photos without watermarksfor use using Unsplash's api.`,
        },
        seasonLocation: {
            image: require("../../images/seasons.png"),
            header: "Geolocation Season Detector",
            description: `A website that detects what season it is based off of your location in the world using a Geolocation api. Currently determines seasons as either 'summer'or 'winter'`,
        }
    };

    return (
        <div className="ui container">
            <h2>
                Projects Collection
            </h2>
            <div className="ui cards">
                <div className="card">
                    <div className="image">
                        <img src={projects.youtube.image} alt="Youtube Clone"/>
                    </div>
                    <div className="content">
                        <div className="header">{projects.youtube.header}</div>
                        <div className="description">
                            <p>
                                {projects.youtube.description}
                            </p>
                        </div>
                    </div>
                </div>
                <div className="card">
                    <div className="image">
                        <img src={projects.stockImage.image} alt="Stock Photos"/>    
                    </div>
                    <div className="content">
                        <div className="header">{projects.stockImage.header}</div>
                        <div className="description">
                            <p>
                                {projects.stockImage.description}
                            </p>
                        </div>
                    </div>
                </div>
                <div className="card">
                    <div className="image">
                        <img src={projects.seasonLocation.image} alt="Geolocation Seasons"/>
                    </div>
                    <div className="content">
                        <div className="header">{projects.seasonLocation.header}</div>
                        <div className="description">
                            <p>
                                {projects.seasonLocation.description}
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Home;