import React from 'react';
import { BrowserRouter as Router, Route, NavLink, Switch, Redirect } from "react-router-dom";
import './App.css';
import Home from './components/Home/Home';
import Clone from './components/YoutubeClone/components/Clone';
import StockImage from './components/StockPhotos/components/StockImage';
import GeoLocation from './components/GeolocationSeasons/GeoLocation';

const App = () => {

    return (
        <Router>
            <div className="ui grid container">            
                <div className="row">
                    <h1>React Project Hub</h1>
                </div>
                <div className="row">
                    <div className="four wide column">
                        <div className="ui vertical fluid tabular menu">
                            <NavLink to="/Home" className="item" activeClassName="active">Home</NavLink>
                            <NavLink to="/Clone" className="item" activeClassName="active">Youtube Clone</NavLink>
                            <NavLink to="/StockImage" className="item" activeClassName="active">Stock Image Search</NavLink>
                            <NavLink to="/GeoLocation" className="item" activeClassName="active">Geolocation Seasons</NavLink>
                        </div>
                    </div>
                    <div className="twelve wide column">
                        <Switch>
                            <Route exact path="/Home" component={Home} />
                            <Route path="/Clone" component={Clone}></Route>
                            <Route path="/StockImage" component={StockImage} />
                            <Route path="/GeoLocation" component={GeoLocation}></Route> 
                            <Redirect to="/Home" />
                        </Switch>
                    </div>
                </div>  
            </div>
        </Router> 
        );
    };

export default App;